* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.btn-container {
  width: 300px;
  justify-content: space-between;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.color-btn {
  width: 100px;
  height: 50px;
  color: #fafafa;
  background-color: #4747bc;
  font-size: 35px;
}

.color-btn:disabled {
  color: #d8bfd8;
  background-color: azure;
}

.color-btn:disabled:hover {
  color: #000;
  background-color: azure;
}

.color-btn:hover {
  color: #000;
  background-color: #0ff;
}

.timer {
  width: 400px;
  justify-content: space-between;
  margin-top: 40px;
  margin-left: 40px;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.value {
  font-size: 40px;
}

